import React from 'react';
import Todos from './components/Todos'
import './App.css';

class App extends React.Component {
  state = {
    todos: [
      {
        id: 1,
        title: 'Take out trash',
        completed: false
      },
      {
        id: 2,
        title: 'Go to the hospital',
        completed: false
      },
      {
        id: 3,
        title: 'Date in the aquarium',
        completed: false
      }
    ]
  }

  eliminateItem = (id) =>{
    this.state.todos.remove(id);
  }

  markComplete = (id) =>{
    this.setState({todos: this.state.todos.map(todo => {
     if(todo.id === id){
       todo.completed = !todo.completed
     }
     return todo; 
    })});
  }

  render(){
    return (
      <div className="App">
       <h1>Hello world</h1>
       <Todos todos={this.state.todos} markComplete={this.markComplete} eliminateItem={this.eliminateItem}/>
      </div>
    );
  }
}

export default App;
